import Vue from 'vue';
import VueRouter from 'vue-router';
import VueResource from 'vue-resource';

//使用vue-router
Vue.use(VueRouter);
//使用vue-resource
Vue.use(VueResource);

//引入主组件
import App from './App';

//引入nav组件
import introduce from 'components/nav/introduce';
import play from 'components/nav/play';
import tips from 'components/nav/tips'
;


var router = new VueRouter({
	mode:"history",
	routes:[{
		path:"/",
		name:"intro",
		component:introduce
	},{
		path:"/play",
		name:"play",
		component:play
	},{
		path:"/tips",
		name:"tips",
		component:tips
	}]
});
new Vue({
	el: '#app',
	router,
	render: h => h(App)
});  



